/* eslint-disable */

import React from 'react';
//import { Link } from 'react-router-dom';
import MenuOutlinedIcon from '@material-ui/icons/MenuOutlined';
import NotificationsNoneOutlinedIcon from '@material-ui/icons/NotificationsNoneOutlined';
import AddToPhotosOutlinedIcon from '@material-ui/icons/AddToPhotosOutlined';
import CreditCardOutlinedIcon from '@material-ui/icons/CreditCardOutlined';
import StorefrontOutlinedIcon from '@material-ui/icons/StorefrontOutlined';

import PersonAdd from '@material-ui/icons/PersonAdd';
import Settings from '@material-ui/icons/Settings';
import Logout from '@material-ui/icons/Logout';

import {
  AppBar,
  Box,
  IconButton,
  Toolbar,
  Menu,
  MenuItem,
  Button,
  Avatar,
  Divider,
  ListItemIcon,
} from '@material-ui/core';
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
const MySwal = withReactContent(Swal)
import userimg from '../../../assets/images/users/user.jpg';
import { useNavigate } from 'react-router';

const Header = (props) => {
  const navigate = useNavigate();
  const logOut = () => {
    handleClose4();
    MySwal.fire({
      title: 'Sure you want to leave?',
      showCancelButton: true,
      confirmButtonText: 'Leave',
      cancelButtonText: `Stay`,
    }).then(async (result) => {
      if (result.isConfirmed) {
        localStorage.clear();
        navigate('/login');
      } else if (result.isDenied) { }
    })
  }
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const [posAnchorEl, setPOSAnchorEl] = React.useState(null);

  const handlePOSClick = (event) => {
    setPOSAnchorEl(event.currentTarget);
  };

  const handlePOSClose = () => {
    setPOSAnchorEl(null);
  };

  // 4
  const [anchorEl4, setAnchorEl4] = React.useState(null);

  const handleClick4 = (event) => {
    setAnchorEl4(event.currentTarget);
  };

  const handleClose4 = () => {
    setAnchorEl4(null);
  };

  const handleRedirect = () => {
    navigate("new/register/user", { replace: true });
  }


  // 5
  const [anchorEl5, setAnchorEl5] = React.useState(null);

  const handleClick5 = (event) => {
    setAnchorEl5(event.currentTarget);
  };

  const handleClose5 = () => {
    setAnchorEl5(null);
  };

  return (
    <AppBar sx={props.sx} elevation={0} className={props.customClass}>
      <Toolbar>
        <IconButton
          color="inherit"
          aria-label="menu"
          onClick={props.toggleMobileSidebar}
          sx={{
            display: {
              lg: 'none',
              xs: 'inline',
            },
          }}
        >
          <MenuOutlinedIcon width="20" height="20" />
        </IconButton>
        <IconButton
          aria-label="menu"
          color="inherit"
          aria-controls="dd-menu"
          aria-haspopup="true"
          onClick={handleClick5}
        >
          <AddToPhotosOutlinedIcon />
        </IconButton>
        <Menu
          id="dd-menu"
          anchorEl={anchorEl5}
          keepMounted
          open={Boolean(anchorEl5)}
          onClose={handleClose5}
          anchorOrigin={{ horizontal: 'left', vertical: 'bottom' }}
          transformOrigin={{ horizontal: 'left', vertical: 'top' }}
          sx={{
            '& .MuiMenu-paper': {
              width: '250px',
              right: 0,
              top: '70px !important',
            },
          }}
        >
          <MenuItem onClick={handleClose5}>
            <Avatar
              sx={{
                width: '35px',
                height: '35px',
              }}
            />
            <Box
              sx={{
                ml: 2,
              }}
            >
              Create User
            </Box>
          </MenuItem>
          <MenuItem onClick={handleClose5}>
            <Avatar
              sx={{
                width: '35px',
                height: '35px',
              }}
            />
            <Box
              sx={{
                ml: 2,
              }}
            >
              Manage Users
            </Box>
          </MenuItem>
          <Divider />
          <MenuItem onClick={handleClose5}>
            <StorefrontOutlinedIcon width="20" height="20" />
            <Box
              sx={{
                ml: 2,
              }}
            >
              Create Shop
            </Box>
          </MenuItem>
          <MenuItem onClick={handleClose5}>
            <StorefrontOutlinedIcon width="20" height="20" />

            <Box
              sx={{
                ml: 2,
              }}
            >
              Manage Shops
            </Box>
          </MenuItem>
        </Menu>
        <Box flexGrow={1} />
        {/* ------------------------------------------- */}
        {/* POS Dropdown */}
        {/* ------------------------------------------- */}
        <IconButton
          aria-label="menu"
          color="inherit"
          aria-controls="pos-menu"
          aria-haspopup="true"
          onClick={handlePOSClick}
        >
          <CreditCardOutlinedIcon width="20" height="20" />
        </IconButton>
        <Menu
          id="pos-menu"
          anchorEl={posAnchorEl}
          keepMounted
          open={Boolean(posAnchorEl)}
          onClose={handlePOSClose}
          anchorOrigin={{ horizontal: 'right', vertical: 'bottom' }}
          transformOrigin={{ horizontal: 'right', vertical: 'top' }}
          sx={{
            '& .MuiMenu-paper': {
              width: '200px',
              right: 0,
              top: '70px !important',
            },
          }}
        >
          <MenuItem onClick={handleClose}>Create POS</MenuItem>
          <MenuItem onClick={handleClose}>Recent Transactions</MenuItem>
          <MenuItem onClick={handleClose}>Product List</MenuItem>
          <MenuItem onClick={handleClose}>Add Product </MenuItem>
        </Menu>
        {/* ------------------------------------------- */}
        {/* POS Dropdown */}
        {/* ------------------------------------------- */}
        {/* ------------------------------------------- */}
        {/* Notifications Dropdown */}
        {/* ------------------------------------------- */}
        <IconButton
          aria-label="menu"
          color="inherit"
          aria-controls="notification-menu"
          aria-haspopup="true"
          onClick={handleClick}
        >
          <NotificationsNoneOutlinedIcon width="20" height="20" />
        </IconButton>
        <Menu
          id="notification-menu"
          anchorEl={anchorEl}
          keepMounted
          open={Boolean(anchorEl)}
          onClose={handleClose}
          anchorOrigin={{ horizontal: 'right', vertical: 'bottom' }}
          transformOrigin={{ horizontal: 'right', vertical: 'top' }}
          sx={{
            '& .MuiMenu-paper': {
              width: '200px',
              right: 0,
              top: '70px !important',
            },
          }}
        >
          <MenuItem onClick={handleClose}>Action</MenuItem>
          <MenuItem onClick={handleClose}>Action Else</MenuItem>
          <MenuItem onClick={handleClose}>Another Action</MenuItem>
        </Menu>
        {/* ------------------------------------------- */}
        {/* End Notifications Dropdown */}
        {/* ------------------------------------------- */}
        {/* ------------------------------------------- */}
        {/* Profile Dropdown */}
        {/* ------------------------------------------- */}
        <Box
          sx={{
            width: '1px',
            backgroundColor: 'rgba(0,0,0,0.1)',
            height: '25px',
            ml: 1,
          }}
        ></Box>
        <Button
          aria-label="menu"
          color="inherit"
          aria-controls="profile-menu"
          aria-haspopup="true"
          onClick={handleClick4}
        >
          <Box
            sx={{
              display: 'flex',
              alignItems: 'center',
            }}
          >
            <Avatar
              src={
                'data:image/png;base64,'
              }
              alt={userimg}
              sx={{
                width: '30px',
                height: '30px',
              }}
            />
          </Box>
        </Button>
        <Menu
          id="profile-menu"
          anchorEl={anchorEl4}
          keepMounted
          open={Boolean(anchorEl4)}
          onClose={handleClose4}
          anchorOrigin={{ horizontal: 'right', vertical: 'bottom' }}
          transformOrigin={{ horizontal: 'right', vertical: 'top' }}
          sx={{
            '& .MuiMenu-paper': {
              width: '250px',
              right: 0,
              top: '70px !important',
            },
          }}
        >
          <MenuItem onClick={handleClose4}>
            <Avatar
              sx={{
                width: '35px',
                height: '35px',
              }}
            />
            <Box
              sx={{
                ml: 2,
              }}
            >
              My account
            </Box>
          </MenuItem>
          <Divider />
          <MenuItem onClick={handleRedirect}>
            <ListItemIcon>
              <PersonAdd fontSize="small" />
            </ListItemIcon>
            Add another account
          </MenuItem>
          <MenuItem onClick={handleClose4}>
            <ListItemIcon>
              <Settings fontSize="small" />
            </ListItemIcon>
            Settings
          </MenuItem>
          <MenuItem onClick={logOut}>
            <ListItemIcon>
              <Logout fontSize="small" />
            </ListItemIcon>
            Logout
          </MenuItem>
        </Menu>
      </Toolbar>
    </AppBar>
  );
};

export default Header;
